<template>
<div class="container">
	<div class="login">
		<div class="login-top col-md-12">
			<div class="col-md-3">
				<a href="/">
					<img src="../assets/img/snlogo.png">
				</a>
			</div>
		   <div class="col-md-9">
			   	<p>为确保您账户的安全及正常使用，依《网络安全法》相关要求，6月1日起会员账户需绑定手机。如您还未绑定，请尽快完成，感谢您的理解及支持！</p>
			   	<span class="write">
			   		<span class="glyphicon glyphicon-edit"></span><a href="">我想对登录提意见</a>
			   	</span>
		   </div>
		</div>
		<div class="login-content col-md-12">
			<img class="lg-bg" src="../assets/img/bg-2.jpg">
			<a href=""></a>
			<div class="loginForm">
				<form action="" method="post">
					<p>
						<a href="" class="yellow">扫码登录</a>
						<a href="">账户登录</a>
					</p>
					<P>
						<input type="text" name="num" v-model="log.num" placeholder="用户名/手机/邮箱/门店用户卡号">
					</P>					
					<p>
						<input type="password" name="password" v-model="log.pass" placeholder="密码">
					</p>
					<p>
						<a href="">短信验证登录</a>
						<a href="">忘记密码？</a>
					</p>
					<p>
						<input type="button" v-on:click.prevent="post" class="logto" value="登录">
					</p>	
					<p>
						<a href="/register">免费注册 有惊喜></a>
					</p>
					<p>
						——————  使用以下账号登录  ——————
					</p>
					<div>
						<a href="">QQ | </a>
						<a href="">微信 | </a>
						<a href="">易付宝 | </a>
						<a href="">门店会员卡</a>
					</div>				
				</form>
			</div>
		</div>
	</div>
</div>
</template>
<script>
export default{
	name:"login",
	data(){
		return{
			log:{
				num:'123',
				pass:'123'
			}
		}
	},
	methods:{
		post:function(){
			this.$axios.post('http://api.tp5.com/user/login',{
				username:this.log.num,
				password:this.log.pass
			}).then(function(data){
				console.log(data.data);
			})
		}
	}
}
</script>
<style src="@/less/login.less" type="less" scoped>
 
</style>


