<template>
	<div class="register">
		<div class="register-top">
		   <img src="../assets/img/snlogo.png">
		   <div>欢迎注册</div>
		   <a href="/login">我已注册，马上登陆 ></a>
		</div>
		<div class="register-content">
			<div class="register-form">
				<form action="" method="post">
					<p>注册领<span style="color: #f60">199</span>元大礼包</p>
					<p>
						<label>手机号码</label>
						<input type="text" name="number" v-model="reg.num" maxlength="11" class="auth" placeholder="请输入手机号">
						<span class="error">手机号码格式不正确！</span>
						<span class="correct">√</span>
						<span class="reajax">该用户已存在!</span>
					</p>
					<p>
						<label>手机验证码</label>
						<input type="text" name="authcode"  placeholder="请输入手机验证码">
						<a href="">获取验证码</a>
					</p>
					<p>
						<label>设置密码</label>
						<input type="password" name="password" v-model="reg.pass" class="auth" placeholder="请输入密码">
						<span class="error">密码长度至少8位！</span>
						<span class="correct">√</span>
					</p>
					<p>
						<input type="checkbox" class="auth" name="checkbox">
						<a href="">《苏宁易购会员章程》《易付宝协议》及《苏宁广告联盟在线协议》</a>
						<span class="error">!!!!!!!!</span>
					</p>
					<p>
						<input type="button" v-on:click.prevent="post" value="提交注册">
					</p>
					<p><a href="">企业用户注册 ></a></p>
				</form>
			</div>
		</div>
	</div>
</template>

<script>
import RegJs from '../../static/js/register.js'
export default{
	name:"register",
	data(){
		return{
			reg:{
				num:'18452524290',
				pass:'202cb962ac59075b964b07152d234b70'
			}
		}
	},
	methods:{
		post:function(){
			this.$axios.post('http://api.tp5.com/user/register',{
				username:this.reg.num,
				password:this.reg.pass
			}).then(function(data){
				console.log(data.data);
			})
		}
	}
}
</script>
<style src="@/less/register.less" type="less" scoped>

</style>


